import Model from '@/service/Model.js'
import apis from '@/config/apis.js'
import axios from '@/extensions/axios.js'

class Article{}

//--about form
Article.scenarios={
	create:['title','content'],
	destroy:['id'],
	edit:['id','title','content'],
	setUser:['id','userId'],
};

Article.createForm=function(scenario){
	var attr=Article.scenarios[scenario];
	var form=Model.createForm();
	attr.forEach(function(item){
		form.attributes[item]="";
	});
	return form;
}

Article.fillData=function(form,data){
	Model.fillData(form,data);
}
Article.resetForm=function(form){
	form.attributes.title="";
	form.attributes.content="";
}


//--api
Article.detail=function(data,success,fail){
	axios.get(apis.articleDetail,{params:data})
	.then(success)
	.catch(fail);
}
Article.search=function(data,success,fail){
	axios.get(apis.articleSearch,{params:data})
	.then(success)
	.catch(fail);
}
Article.comments=function(data,success,fail){
	console.log(apis.articleComments);
	axios.get(apis.articleComments,{params:data})
	.then(success)
	.catch(fail);
}

Article.create=function(data,success,fail){
	data=Model.format(data);
	axios.post(apis.articleCreate,data)
	.then(success)
	.catch(fail);
}
Article.destroy=function(data,success,fail){
	data=Model.format(data);
	axios.post(apis.articleDestroy,data)
	.then(success)
	.catch(fail);
}
Article.edit=function(data,success,fail){
	data=Model.format(data);
	axios.post(apis.articleEdit,data)
	.then(success)
	.catch(fail);
}
Article.setUser=function(data,success,fail){
	data=Model.format(data);
	axios.post(apis.articleSetUser,data)
	.then(success)
	.catch(fail);
}

Article.behaviors={
	articleCreate:function(form,callback){
		var vm=this;
		Article.create(form.attributes,function(response){
			if(response.data.result){
				if(callback=="append"){
					vm.dataArticles().push(response.data.article);
					// vm.$message.success("文章添加成功");
					form.flag=false;
					Article.resetForm(form);
					vm.dataArticleFlash(response.data.article.id);
				}else{
					vm.$router.push("/article/"+response.data.article.id);
				}
			}else{
				vm.$message.error(response.data.message);
			}
		})
	},
	articleDestroy:function(article,callback){
		var vm=this;
		vm.$confirm('确定要删除"'+article.title+'"吗？','提示')
		.then(function(){
			Article.destroy({id:article.id},function(response){
				if(response.data.result){
					if(callback=="remove"){
						var articles=vm.dataArticles();
						articles.forEach(function(item,k){
							if(item.id==article.id){
								articles.splice(k,1);
							}
						});
						// vm.$message.success("删除成功")
					}else{
						vm.$router.push("/article/search");
					}
				}else{
					vm.$message.error(response.data.message)
				}
			});
		});
	},
	articleEdit:function(form,callback){
		var vm=this;
		Article.edit(form.attributes,function(response){
			if(response.data.result){
				if(callback=="replace"){
					vm.dataArticleReplace(response.data.article);
					form.flag=false;
					// vm.$message.success("修改成功");
					vm.dataArticleFlash(response.data.article.id);
				}else{
					vm.$router.push("/article/"+response.data.article.id);
				}
			}else{
				vm.$message.error(response.data.message);
			}
		})
	},
	articleSetUser:function(form,callback){
		var vm=this;
		Article.setUser(form.attributes,function(response){
			if(response.data.result){
				var article=response.data.article;
				if(callback=="replace"){
					vm.dataArticleReplace(article);
					form.flag=false;
					//flash
					vm.dataArticleFlash(article.id);
				}else{
					vm.$router.push("/article/"+article.id);
				}
			}else{
				alert(response.data.message);
			}
		})
	},
	articleCreateFlag:function(flag){
      	this.forms.articleCreate.flag=flag;
    },
    articleEditFlag:function(id){
		var form=this.forms.articleEdit;
		if(id==false){
			form.flag=false;
		}else{
			form.flag=true;
			var article=this.dataArticle(id);
			Article.fillData(form,article);
		}
    },
    articleSetUserFlag:function(id){
		var form=this.forms.articleSetUser;
		if(id==false){
			form.flag=false;
		}else{
			form.flag=true;
			var article=this.dataArticle(id);
			Article.fillData(form,article);
		}
    },
    dataArticleReplace:function(article){
		var old=this.dataArticle(article.id);
		for(var i in article){
			if(article.hasOwnProperty(i)){
				if(old.hasOwnProperty(i)){
					old[i]=article[i];
				}else{
					this.$set(old,i,article[i]);
				}
			}
		}
    },
    dataArticleFlash:function(id){
    	var old=this.dataArticle(id);
		this.$set(old,"_flash",true);
		setTimeout(function(){
			old._flash=false;
		},2000);
    }
};

Article.attach=function(vm){
	for(var i in Article.behaviors){
		if(Article.behaviors.hasOwnProperty(i)){
			(function(i){
				var behavior=Article.behaviors[i];
				vm.methods[i]=function(){
					behavior.apply(this,arguments);
				};
			})(i);
		}
	}
}


export default Article;


