class ErrorHandler{

}

ErrorHandler.handleWatch=function(error,to,from,vm){
	var status=error.response.status;
	if(status==401){
	    delete localStorage.auth;
	    localStorage.returnUrl=to.path;
	    vm.$router.replace("/site/login");
	}else{
      	vm.$router.replace('/error/'+status);
	}
}

ErrorHandler.handleGet=function(error,to,from,next){
	next(function(vm){
		ErrorHandler.handleWatch(error,to,from,vm);
	});
}

ErrorHandler.handlePost=function(error){
	alert(error.response.data.message);
}

export default ErrorHandler;


