<template>
<div class="container" v-if="init">
  <sub-header></sub-header>
  <div>我的文章</div>
  <div class="articles">
    <transition-group tag="div" name="fade">
      <sub-article 
        v-for="article in source.dataProvider.models" 
        :article="article" 
        :key="article.id"
        v-on:articleDestroy="articleDestroy"
        v-on:articleEditFlag="articleEditFlag"
        v-on:articleSetUserFlag="articleSetUserFlag">
      </sub-article>
    </transition-group>
  </div>
  <div class="buttons">
    <router-link to="/article/create">添加文章</router-link>
    <el-tooltip class="item" effect="dark" content="Top Left 提示文字" placement="top-start">
      <el-button @click="articleCreateFlag(true)">弹窗加文章</el-button>
    </el-tooltip>
    
  </div>
  <sub-footer></sub-footer>
  <article-sub-create
    :form="forms.articleCreate"
    @articleCreate="articleCreate"
  ></article-sub-create>
  <article-sub-edit
    :form="forms.articleEdit"
    @articleEdit="articleEdit"
  ></article-sub-edit>
  <article-sub-set-user
    :form="forms.articleSetUser"
    :users="source.users"
    @articleSetUser="articleSetUser"
  ></article-sub-set-user>
</div>
</template>

<script>
import Article from '@/service/Article.js'
import SubArticle from '@/components/article/sub/One.vue'
import ArticleSubCreate from '@/components/article/sub/Create.vue'
import ArticleSubEdit from '@/components/article/sub/Edit.vue'
import ArticleSubSetUser from '@/components/article/sub/SetUser.vue'
import ErrorHandler from '@/helpers/ErrorHandler.js'

var ret={
  data () {
    return {
      init:false,
      source:null,
      forms:{
        articleCreate:Article.createForm("create"),
        articleEdit:Article.createForm("edit"),
        articleSetUser:Article.createForm("setUser")
      }
    }
  },
  components:{
    'sub-article':SubArticle,
    'article-sub-create':ArticleSubCreate,
    'article-sub-edit':ArticleSubEdit,
    'article-sub-set-user':ArticleSubSetUser,
  },
  beforeRouteEnter:function(to,from,next){
    Article.search({},function(response){
      next(function(vm){
        vm.source=response.data;
        vm.init=true;
      })
    },function(error){
      ErrorHandler.handleGet(error,to,from,next);
    })
  },
  methods:{
    dataArticles:function(){
      return this.source.dataProvider.models;
    },
    dataArticle:function(id){
      var article=null;
      this.dataArticles().forEach(function(item,k){
        if(item.id==id){
          article=item;
        }
      });
      return article;
    }
  }
}

Article.attach(ret);

export default ret;
</script>



