<template>
	<div class="comment">
		<p>	
		<span>{{comment.text}}</span>
		<span>----</span> 
		<span>{{comment.createTime}}</span>
		<a href="javascript:void(0)" @click="commentDestroy(comment,'remove')">delete</a>
		</p>
	</div>
</template>

<script type="text/javascript">
	export default {
		data:function(){
			return {};
		},
		props:["comment"],
		methods:{
			commentDestroy:function(comment,callback){
				this.$emit("commentDestroy",comment,callback);
			}
		}
	};
</script>


