import Index from '@/components/site/Index.vue'
import Login from '@/components/site/Login.vue'
import Test from '@/components/site/Test.vue'

export default [
	{path:'/site/index',alias:"/",component:Index},
	{path:'/site/login',component:Login},
	{path:'/site/test',component:Test}
]


