import Model from '@/service/Model.js'
import apis from '@/config/apis.js'
import axios from '@/extensions/axios.js'

class User{}

//--about form
User.scenarios={
	refreshToken:["username","password"],
};

User.createForm=function(scenario){
	var attr=User.scenarios[scenario];
	var form=Model.createForm();
	attr.forEach(function(item){
		form.attributes[item]="";
	});
	return form;
}

User.fillData=function(form,data){
	Model.fillData(form,data);
}
User.resetForm=function(form){
}


//--api
User.me=function(data,success,fail){
	axios.get(apis.userMe,{params:data})
	.then(success)
	.catch(fail);
}
User.refreshToken=function(data,success,fail){
	data=Model.format(data);
	axios.post(apis.siteRefreshToken,data)
	.then(success)
	.catch(fail);
}

User.behaviors={
};

User.attach=function(vm){
	for(var i in User.behaviors){
		if(User.behaviors.hasOwnProperty(i)){
			(function(i){
				var behavior=User.behaviors[i];
				vm.methods[i]=function(){
					behavior.apply(this,arguments);
				};
			})(i);
		}
	}
}


export default User;


