<template>
<div class="container" v-if="init">
  <sub-header></sub-header>
  <div>文章详情</div>
  <div>
  	<p>id:{{source.article.id}}</p>
  	<p>title:{{source.article.title}}</p>
  	<p>content:{{source.article.content}}</p>
    <p v-show="source.article.user">username:{{source.article.user.username}}</p>

    <div class="comment-list" v-show="source.article.comments">
    <transition-group name="fade" tag="div">
      <sub-comment 
      v-for="(comment,index) in source.article.comments"
      :key="index" 
      :comment="comment"
      v-on:commentDestroy="commentDestroy"
      ></sub-comment>
    </transition-group>
    </div>

  </div>
  <div class="buttons">
    <a href="javascript:void(0)" @click="articleDestroy(source.article)">删除</a>
    <a href="javascript:void(0)" @click="articleSetUserFlag(source.article.id)">修改作者</a>
    <a href="javascript:void(0)" @click="commentCreateFlag(forms.commentCreate,source.article.id)">评论</a>
    <router-link :to="'/article/'+source.article.id+'/comments'">评论列表</router-link>
    <router-link to="/article/search">返回列表</router-link>
    <router-link :to="'/article/'+(source.article.id+1)">next</router-link>
  </div>
  <sub-footer></sub-footer>
  <comment-sub-create 
    :form="forms.commentCreate"
    @commentCreate="commentCreate"
    ></comment-sub-create>
  <article-sub-set-user 
    :form="forms.articleSetUser"
    :users="source.users"
    @articleSetUser="articleSetUser"
    ></article-sub-set-user>
</div>
</template>

<script>
import Article from '@/service/Article.js'
import Comment from '@/service/Comment.js'
import SubComment from '@/components/comment/sub/One.vue'
import CommentSubCreate from '@/components/comment/sub/Create.vue'
import ArticleSubSetUser from '@/components/article/sub/SetUser.vue'
import ErrorHandler from '@/helpers/ErrorHandler.js'

var ret={
  data () {
    return {
      init:false,
      source:null,
      forms:{
        articleSetUser:Article.createForm('setUser'),
        commentCreate:Comment.createForm("create")
      }
    }
  },
  components:{
    'sub-comment':SubComment,
    'comment-sub-create':CommentSubCreate,
    'article-sub-set-user':ArticleSubSetUser,
  },
  beforeRouteEnter:function(to,from,next){
    Article.detail({id:to.params.id},function(response){
      next(function(vm){
        vm.source=response.data;
        vm.init=true;
      })
    },function(error){
      ErrorHandler.handleGet(error,to,from,next);
    });
  },
  watch:{
    '$route':function(to,from){
      var vm=this;
      Article.detail({id:to.params.id},function(response){
        vm.source=response.data;
      },function(error){
        ErrorHandler.handleWatch(error,to,from,vm);
      });
    }
  },
  methods:{
    dataArticle:function(id){
      return this.source.article.id==id?this.source.article:null;
    },
    dataComments:function(){
      return this.source.article.comments;
    }
  }
}
Article.attach(ret);
Comment.attach(ret);

export default ret;
</script>



