<template>
	<div>
	<p v-if="source.auth">
		<span>欢迎:{{source.auth.user.username}}</span>
		<a href="javascript:void(0)" @click="logout">退出</a>
	</p>
	<p v-else>
		<span>请先</span>
		<router-link to="/site/login">登录</router-link>
	</p>
	</div>
</template>
<script type="text/javascript">
import User from '@/service/User.js'

export default {
	data:function(){
		return {
			source:{
				auth:null
			},
		};
	},
	created:function(){
		var vm=this;
		if(localStorage.auth){
			vm.source.auth=JSON.parse(localStorage.auth);
		}
	},
	methods:{
		logout:function(){
			delete localStorage.auth;
			this.$router.push('/site/login');
		}
	}
};
</script>


