<template>
  <el-dialog
    title="修改文章用户"
    v-model="form.flag">
    <form
      @submit.prevent="articleSetUser(form,'replace')">
      <el-select v-model="form.attributes.userId" placeholder="请选择用户">
        <el-option
          v-for="user in users"
          :key="user.id"
          :value="user.id"
          :label="user.username"
        ></el-option>
      </el-select>
      <input type="submit" value="提交">
      <input type="button" value="取消" @click="form.flag=false">
    </form>
  </el-dialog>
</template>
<script type="text/javascript">
  export default {
    data:function(){
      return {};
    },
    props:['form','users'],
    methods:{
      articleSetUser:function(form,callback){
        this.$emit("articleSetUser",form,callback);
      }
    }
  };
</script>


