<template>
<div class="article" :class="{flash:isFlash()}">
	<router-link :to="'/article/'+article.id">
		{{article.title}}
	</router-link>
	<span v-if="article.user">{{article.user.username}}</span>

	&nbsp;&nbsp;&nbsp;
	<a href="javascript:void(0)" @click="articleDestroy(article,'remove')">delete</a>
	&nbsp;&nbsp;&nbsp;
	<a href="javascript:void(0)" @click="articleEditFlag(article.id)">edit</a>
	&nbsp;&nbsp;&nbsp;
	<a href="javascript:void(0)" @click="articleSetUserFlag(article.id)">set-user</a>
</div>
</template>

<script>

export default {
	props:["article"],
	methods:{
		articleDestroy:function(article,callback){
			this.$emit("articleDestroy",article,callback);
		},
		articleEditFlag:function(id){
			this.$emit("articleEditFlag",id);
		},
		articleSetUserFlag:function(id){
			this.$emit("articleSetUserFlag",id);
		},
		isFlash:function(){
			return this.article.hasOwnProperty("_flash") && this.article._flash;
		}
	}
}
</script>



