<template>
	<div style="width:500px;margin:0 auto 100px">
		<div>
			<p>radio</p>
			<el-radio-group v-model="radio.value" @change="radioValue()">
			    <el-radio :label="3">备选项</el-radio>
			    <el-radio :label="6">备选项</el-radio>
			    <el-radio :label="9">备选项</el-radio>
			</el-radio-group>
		</div>
		<div>
			<p>select</p>
			<el-select v-model="select.value" placeholder="请选择" @change="selectValue()">
				<el-option
					v-for="program in programs"
					:key="program.id"
					:label="program.name"
					:value="program.id">
			    </el-option>
			</el-select>
		</div>
		<div>
			<p>switch</p>
			<el-switch
				v-model="switch2.value"
				on-text=""
				off-text=""
				@change="switchValue()"
			></el-switch>
		</div>
		<div>
			<p>slider</p>
			<el-slider
				v-model="slider.value"
				:step="10"
				show-stops
				@change="sliderValue()"
				:show-tooltip="true"
			></el-slider>
		</div>
	</div>
</template>

<script type="text/javascript">
export default {
	data:function(){
		return {
			radio:{
				value:""
			},
			select:{
				value:"0"
			},
			switch2:{
				value:true
			},
			slider:{
				value:10
			},
			programs:[
				{id:"0",name:"无"},
				{id:"1",name:"java"},
				{id:"2",name:"css"},
				{id:"3",name:"php"}
			]
		}
	},
	methods:{
		selectValue:function(){
			console.log(this.select.value);
		},
		radioValue:function(){
			console.log(this.radio.value);
		},
		switchValue:function(){
			console.log(this.switch2.value);
		},
		sliderValue:function(){
			console.log(this.slider.value);
		}
	}
}

</script>


