class Model{
	
}

Model.format=function(data){
	var param = new URLSearchParams();
	param.append("_json",JSON.stringify(data));
	return param;
}
Model.createForm=function(){
	return {
		flag:false,
		message:false,
		attributes:{},
		doing:false,
	};
}
Model.fillData=function(form,data){
	for(let i in form.attributes){
		if(form.attributes.hasOwnProperty(i) && data.hasOwnProperty(i)){
			form.attributes[i]=data[i];
		}
	}
}

Model.attach=function(vm,behaviors){
	
}

export default Model;


