import Detail from '@/components/article/Detail.vue'
import Search from '@/components/article/Search.vue'

const Comments = resolve => require(['@/components/article/Comments.vue'], resolve);
const Create = resolve => require(['@/components/article/Create.vue'], resolve);

export default [
	{path:'/article/search',component:Search},
	{path:'/article/create',component:Create},
	{path:'/article/:id(\\d+)/comments',component:Comments},
	{path:'/article/:id(\\d+)',component:Detail},
]


