<template>
  <el-dialog
    title="添加文章"
    v-model="form.flag">
    <form
      @submit.prevent="articleCreate(form,'append')">
      <input type="text" v-model="form.attributes.title">
      <input type="text" v-model="form.attributes.content">
      <input type="submit" value="提交">
      <input type="button" value="取消" @click="form.flag=false">
    </form>
  </el-dialog>
</template>
<script type="text/javascript">
  export default {
    data:function(){
      return {};
    },
    props:['form'],
    methods:{
      articleCreate:function(form,callback){
        this.$emit("articleCreate",form,callback);
      }
    }
  };
</script>


