import axios from "@/extensions/axios.js"
import apis from "@/config/apis.js"
import Model from '@/service/Model.js'

class Comment{}

Comment.scenarios={
	create:["text","articleId"],
	destroy:["id"],
};

Comment.createForm=function(scenario){
	var attr=Comment.scenarios[scenario];
	var form=Model.createForm();
	attr.forEach(function(item){
		form.attributes[item]="";
	});
	return form;
}

Comment.behaviors={
	commentCreate:function(form,callback){
		var vm=this;
		form.doing=true;
		Comment.create(form.attributes,function(response){
			if(response.data.result){
				if(callback=="append"){
					vm.dataComments().push(response.data.comment);
					vm.commentCreateFlag(form,false);
				}else{
					vm.$router.push("/comment/"+response.data.comment.id);
				}
			}else{
				alert(response.data.message);
			}
			form.doing=false;
		});
	},
	commentCreateFlag:function(form,articleId){
		if(articleId==false){
			form.flag=false;
		}else{
			form.flag=true;
			form.attributes.articleId=articleId;
			form.attributes.text="";
		}
	},
	commentDestroy:function(comment,callback){
		var vm=this;
		vm.$confirm("确定要删除评论： "+comment.text+" 吗？")
		.then(function(){
			Comment.destroy({id:comment.id},function(response){
				if(response.data.result){
					if(callback=="remove"){
						vm.dataComments().forEach(function(item,k){
							if(item.id==comment.id){
								vm.dataComments().splice(k,1);
							}
						});
						vm.$message.success("删除成功");
					}else{
						vm.$router.push("/comment/list");
					}
				}else{
					vm.$message.error(response.data.message);
				}
			});
		});
	},
};

Comment.create=function(data,success,fail){
	axios.post(apis.commentCreate,Model.format(data))
	.then(success)
	.catch(fail);
}
Comment.destroy=function(data,success,fail){
	axios.post(apis.commentDestroy,Model.format(data))
	.then(success)
	.catch(fail);
}


Comment.attach=function(vm){
	for(var i in Comment.behaviors){
		if(Comment.behaviors.hasOwnProperty(i)){
			(function(i){
				var behavior=Comment.behaviors[i];
				vm.methods[i]=function(){
					behavior.apply(this,arguments);
				};
			})(i);
		}
	}
}

export default Comment;


