<template>
  <div class="container">
      <form @submit.prevent="login">
        <input type="text" v-model="forms.refreshToken.attributes.username"/>
        <input type="password" v-model="forms.refreshToken.attributes.password"/>
        <input type="submit" value="登录" />
      </form>
  </div>
</template>

<script>
import User from '@/service/User.js'

export default {
  data () {
    return {
      source:null,
      forms:{
        refreshToken:User.createForm("refreshToken")
      }
    }
  },
  beforeRouteEnter:function(to,from,next){
    if(localStorage.auth){
      next("/site/index");
    }else{
      next();
    }
  },
  created:function(){
    this.forms.refreshToken.attributes.client=1;
  },
  methods:{
    login:function(){
      var vm=this;
      User.refreshToken(vm.forms.refreshToken.attributes,function(response){
        if(response.data.result){
          localStorage.auth=JSON.stringify(response.data.auth);

          if(localStorage.returnUrl){
            let returnUrl=localStorage.returnUrl;
            delete localStorage.returnUrl;
            vm.$router.push(returnUrl);
          }else{
            vm.$router.push("/site/index");
          }
        }else{
          vm.$message.error(response.data.message);
        }
      })
    }
  }
}
</script>



