<template>
	<el-dialog 
	title="添加评论"
	v-model="form.flag">
		<form @submit.prevent="commentCreate(form,'append')">
			<el-input type="text" v-model="form.attributes.text"></el-input>
			<el-button @click="commentCreate(form,'append')" :loading="form.doing">提交</el-button>
		</form>
	</el-dialog>
</template>
<script type="text/javascript">
	export default {
		props:["form"],
		methods:{
			commentCreate:function(form,callback){
				this.$emit("commentCreate",form,callback);
			}
		}
	};
</script>


